<template>
  <div class="home" @wheel.stop.prevent="handleWheel">
      <div class="wheel" v-show="num == 1" ref="wheel">
      <div class="wheel page1">
        <el-row :gutter="20" style="width: 50%;margin:0 auto;height:50%;left: 25%;top: 50%;transform: translate(-50%,-50%);">
          <el-col :span="12" v-for="(item, index) in pageData" :key="item.id" style="height:50%;">
            <el-card @click.native="clickCard(index)" style="height: 90%;" :class="'page'+(index+1)"></el-card>
          </el-col>
        </el-row>
      </div>
    </div>
    <div class="wheel page2" v-show="num == 2">
      <div  class="page2_but">
        <div class="but">
          <p>一个使用vant的手机版的页面</p>
          <el-button type="primary" @click="changeRouter('findF')">手机页面</el-button>
          <hr>
          <p>一个奇怪的页面</p>
          <el-button type="primary" @click="changeRouter('mClearance')">打开试试</el-button>
        </div>
        <!-- <router-link :to="{path: '/findF', params: {num: num}}">点我</router-link> -->
      </div>
    </div>
    <div class="wheel page3" v-show="num == 3">
      <div  class="page2_but">
        <div class="but">
          <el-input class="inp" v-model="input" placeholder="请输入验证" @change="goMyStud" show-password/>
          <el-button type="primary" @click="goMyStud">点我</el-button>
        </div>
        <!-- <router-link :to="{path: '/findF', params: {num: num}}">点我</router-link> -->
      </div>
    </div>
    <div class="wheel page4" v-show="num == 4">
      <div  class="page2_but">
        <div class="but2">
          <page4Index v-if="isShow" :myTime="myTime" :along="along" @changeNum="isShow = false"/>
          <SetMyTime v-else @changeMunber="changeMunber"/>
        </div>
      </div>
    </div>
    <el-button v-show="num != 1" type="primary" class="black" round @click="blackHome">返回首页</el-button>
  </div>
</template>

<script>
import page4Index from './page4/page4Index.vue'
import SetMyTime from './page4/setMyTime.vue'

export default {
  name: 'HomeView',
  data() {
    return {
      input: '926',
      lastTime: null,
      num: 1,
      maxNum: 4,
      offsHeight: 0,
      isShow: true,
      myTime: 0,
      along: true,
      pageData: [{title: '第一页',id: '1'},{title: '第二页',id: '2'},{title: '第三页',id: '3'},{title: '第四页',id: '4'}]
    }
  },
  components: {
    page4Index,
    SetMyTime
  },
  created() {
    this.lastTime = new Date()
  },
  mounted() {
    this.offsHeight = this.$refs.wheel.offsetHeight
  },
  methods: {
    //防止滚动的多次触发
    throttle (fn, gapTime) {
      let _this = this
      return function () {
        let _nowTime = +new Date()
        if (_nowTime - _this.lastTime > gapTime || !_this.lastTime) {
          fn(...arguments)
          _this.lastTime = _nowTime
        }
      }
    },
    // 取消页面的滚动特效
    handleWheel(e) {
      // e.stopPropagation()
      this.throttle(this.pageUpOrDown,60)(e)
    },
    //页面上下滚动
    pageUpOrDown (e) {
      if (e.deltaY > 0) {
        this.num ++
        if (this.num > this.maxNum) {
          this.num = 1
        }
        window.scrollTo(0,this.offsHeight*this.num)
      } else if (e.deltaY < 0) {
        this.num --
        if (this.num < 1) {
          this.num = this.maxNum
        }
        window.scrollTo(0,this.offsHeight*this.num)
      }
    },
    // 点击首页的卡片，跳转到相应页
    clickCard(index) {
      this.num = index + 1
      window.scrollTo(0,this.offsHeight*this.num)
    },
    // 返回首页
    blackHome() {
      this.num = 1
      window.scrollTo(0,this.offsHeight*this.num)
    },
    //点击跳转到其他router页面
    changeRouter(name) {
      if (this.num === 2) {
        this.$router.push({name: name,params: {num: this.num}})
      }
    },
    //跳转到我的页面
    goMyStud() {
      if (this.input === '926') {
        this.$router.push({name: 'myStud',params: {num: this.num}})
      } else {
        this.$message({
          message: '请输入正确的验证信息',
          type: 'warning'
        });
      }
    },
    changeMunber(data) {
      if (data) {
        this.myTime = data.num
        this.along = data.isAlong
      }
      this.isShow = true
    }
  }
}
</script>

