<template>
  <div>
      <el-form ref="form" class="form" :model="form">
          <span>最高24小时</span>
        <el-form-item label="时">
            <el-input-number v-model="form.hours" :min="0" :max="24"></el-input-number>
        </el-form-item>
        <el-form-item label="分">
            <el-input-number v-model="form.minutes" :min="0"></el-input-number>
        </el-form-item>
        <el-form-item label="秒">
            <el-input-number v-model="form.seconds" :min="0"></el-input-number>
        </el-form-item>
        <el-form-item>
            <el-button type="primary" @click="onSubmit(false)">返回</el-button>
            <el-button type="primary" @click="isAlong = true; onSubmit(true)">顺序</el-button>
            <el-button type="primary" @click="isAlong = false; onSubmit(true)">倒序</el-button>
        </el-form-item>
      </el-form>
  </div>
</template>

<script>
export default {
    data() {
        return {
            form: {
                hours: 0,
                minutes: 0,
                seconds: 0
            },
            isAlong: true
        }
    },
    methods: {
        onSubmit(bool) {
            if (bool) {
                let num = this.form.hours*3600 + this.form.minutes * 60 + this.form.seconds
                if (num > 24*3600) {
                    this.$message({
                        message: '时间大于24小时',
                        type: 'warning'
                    });
                } else {
                    this.$emit("changeMunber",{isAlong: this.isAlong, num: num})
                }
            } else {
                this.$emit("changeMunber")
            }
        }
    }
}
</script>

<style lang="less" scoped>
.form {
    margin: 80px;
}
</style>