<template>
  <div>
    <span>拟定的时间是：
      <span v-if="Math.trunc(myTime/3600)">{{Math.trunc(myTime/3600)}}小时</span>
      <span v-if="Math.trunc((myTime%3600)/60)">{{Math.trunc((myTime%3600)/60)}}分钟</span>
      <span v-if="myTime%60">{{myTime%60}}秒</span>
    </span>
    <el-row class="showTime">
      <el-col :span="7"><span v-if="showTime/3600 < 10">0</span>{{Math.trunc(showTime/3600)}}</el-col>
      <el-col :span="1">:</el-col>
      <el-col :span="7"><span v-if="(showTime%3600)/60 < 10">0</span>{{Math.trunc((showTime%3600)/60)}}</el-col>
      <el-col :span="1">:</el-col>
      <el-col :span="7"><span v-if="showTime%60 < 10">0</span>{{showTime%60}}</el-col>
    </el-row>
    <el-button type="primary" :disabled="butDisabled" @click="butDisabled = true; isActidve = true; getTime()">开始</el-button>
    <el-button type="primary" @click="butDisabled = false; isActidve = false">停止</el-button>
    <el-button type="primary" @click="changeNumber">编辑</el-button>
    <el-button type="primary" @click="resetTime">重置</el-button>
    <el-button type="primary" @click="changeAlong" v-if="isAlong">顺数</el-button>
    <el-button type="primary" @click="changeAlong" v-if="!isAlong">倒数</el-button>
    <el-button type="primary" 
    @click="isActidve = true;
    getBeijing()"
    v-if="!isBeijing"
    >北京时间</el-button>
    <el-button type="primary" 
    @click="resetTime"
    v-if="isBeijing"
    >计时器</el-button>
  </div>
</template>

<script>
export default {
    data() {
        return {
            isActidve: false,
            isAlong: true,
            showTime: 0,
            isBeijing: false,
            butDisabled: false
            }
    },
    props: ['myTime','along'],
    created() {
      this.isAlong = this.along
      this.resetTime()
    },
    methods: {
      getBeijing() {
        this.isBeijing = true
        let timeData = new Date()
        let hoursData = timeData.getHours()
        let minData = timeData.getMinutes()
        let secData = timeData.getSeconds()
        this.showTime = hoursData * 3600 + minData * 60 + secData
        this.getTime()
      },
      // 修改显示的时间，以秒为单位
      getTime() {
      setTimeout(() => {
        if (this.isActidve) {
          if (this.isBeijing) {
            let timeData = new Date()
            let hoursData = timeData.getHours()
            let minData = timeData.getMinutes()
            let secData = timeData.getSeconds()
            this.showTime = hoursData * 3600 + minData * 60 + secData
          } else {
            if (this.isAlong) {
              this.showTime++
              if (this.showTime == this.myTime) {
                this.isActidve = false
                this.$message({
                  message: '时间结束',
                  type: 'success'
                });
                return
              }
            } else {
              this.showTime--
              if (this.showTime == 0) {
                this.isActidve = false
                this.$message({
                  message: '时间结束',
                  type: 'success'
                });
                return
              }
            }
          }
          this.getTime()
        } else {
          return
        }
      }, 1000);
      },
      changeNumber() {
        this.$emit('changeNum')
      },
      // 重置时间
      resetTime() {
        this.isBeijing = false;
        this.butDisabled = false; 
        this.isActidve = false
        if (this.isAlong) {
          this.showTime = 0
        } else {
          this.showTime = this.myTime
        }
      },
      // 改变顺倒
      changeAlong() {
        this.isBeijing = false
        this.butDisabled = false
        this.isActidve = false
        if (this.isAlong) {
          this.isAlong = !this.isAlong
          this.showTime = this.myTime
        } else {
          this.isAlong = !this.isAlong
          this.showTime = 0
        }
      }
    }
}
</script>

<style lang="less" scoped>
.showTime {
  font-size: 120px;
}
</style>