import Vue from 'vue'
import VueRouter from 'vue-router'
import HomeView from '../views/HomeView.vue'
import findF from '../views/page2/findF.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'home',
    component: HomeView
  },
  {
    path: '/findF',
    name: 'findF',
    component: findF
  },
  {
    path: '/testFind',
    name: 'testFind',
    component: () => import('../views/page2/testFind.vue')
  },
  {
    path: '/myStud',
    name: 'myStud',
    component: () => import('../views/page3/myStud.vue')
  },
  {
    path: '/mClearance',
    name: 'mClearance',
    component: () => import('../views/games/mClearance.vue')
  }
  // {
  //   path: '/about',
  //   name: 'about',
  //   // route level code-splitting
  //   // this generates a separate chunk (about.[hash].js) for this route
  //   // which is lazy-loaded when the route is visited.
  //   component: () => import(/* webpackChunkName: "about" */ '../views/AboutView.vue')
  // }
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
